.tic-tac{
    margin-top: 100px;
    margin-bottom: 100px;
    .board {
        display: grid;
        grid-template-columns: repeat(3, 100px);
        grid-gap: 5px;
        margin: auto;
        max-width: 310px;
      }
      .cell {
        width: 100px;
        height: 100px;
        text-align: center;
        font-size: 60px;
        border: 1px solid black;
        cursor: pointer;
      }
      .text-box{
        margin-top: 50px;
        text-align: center;
        max-width: 500px;
        margin: auto;
        h3{
            margin-top: 64px;
        }
      }
}
