

.sudoku-fill{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #7c7c7c;
    font-family: Arial, Helvetica, sans-serif;
    user-select: none;
    .wrap{
        display: flex;
        flex-direction: column;
        justify-content: center;
        .text-part{
            text-align: center;
            color: #fff;
            margin-bottom: 100px;
        }
        .grid{
            display: grid;
            grid-template-columns: repeat(9, auto);
            grid-template-rows: repeat(9, auto);
            gap: 0.8vmin;
            margin-top: 50px;
            margin-bottom: 50px;
            .cellz {
                display: flex;
                justify-content: center;
                align-items: center;
                height: 8vmin;
                width: 8vmin;
                border-radius: 0.5vmin;
                background-color: #f2f2f2;
                color: #000;
                font-size: 3vmin;
                cursor: pointer;
                
            }
            .filled{
                background-color: #ccc2c2;
            }
            .highlighted{
                background-color: #e0d6ba;
            }
            .selected{
                background-color: #c6c276;
            }
            .error{
                background-color: #d09e9e;
                color: #c63838;
            }

            .cellz:nth-child(3),
                .cellz:nth-child(6) {
                    margin-right: 2vmin;
                }

                .cellz:nth-child(19),
                .cellz:nth-child(46) {
                    margin-bottom: 2vmin;
                }
        }
        .numbers{
            display: grid;
            grid-template-columns: repeat(5, auto);
            gap: 0.5vmin;
            margin-top: 2vmin;
            margin-bottom: 50px;
            .number,
            .remove{
                display: flex;
                justify-content: center;
                align-items: center;
                height: 8vmin;
                border-radius: 0.5vmin;
                background-color: #f2f2f2;
                font-size: 3vmin;
                counter-reset: #000;
                cursor: pointer;
            }
        }

        .shake {
            animation: shake 0.5s ease-in-out;
        }
            @keyframes shake {
                0% {
                    transform: scale(1.2);
                }
                25% {
                    transform: scale(1.2) rotate(30deg);
                }
                50% {
                    transform: scale(1.2) rotate(-30deg);
                }
            }

            .zoom {
                animation: zoom 0.5s ease-in-out;
            }
            @keyframes zoom {
                0% {
                    transform: scale(1.2);
                }
                
            }
        
       
        
    }
}
