.first-screen{
    background-color: rgb(62, 84, 84);
    padding: 24px;
    .col-md-6{     
        background-repeat: no-repeat;
        background-size: cover;
        
        text-align: center;
        padding: 24px;
        margin-top: 5px;
        margin-bottom: 5px;
        &:hover{
            transform: scale(1.1);
            transition: transform 0.3s;
        }
    }
    .tic-tac-toe{
        background-image: url(assets/img/tic-tac.jpg);
        height: 500px;
        width: 100%;
        border-radius: 30px;
    }
    .sudoku{
        background-image: url(assets/img/sudoku.png);
        height: 500px;
        width: 100%;
        border-radius: 30px;
    }
    .saper{
        background-image: url(assets/img/bomber.png);
        height: 500px;
        width: 100%;
        border-radius: 30px;
    }
    .snake{
        background-image: url(assets/img/snake.png);
        height: 500px;
        width: 100%;
        border-radius: 30px;
    }
}